.contact {
  width: 90%;
  height: 100%;
  min-height: 70vh;
  margin: 0 5%;
  @include verticalCenter;
  flex-direction: column;
  justify-content: space-between;

  form {
    @include verticalCenter;
    flex-direction: column;
    min-height: 50vh;
    justify-content: space-around;
    width: 80%;
    @media screen and (max-width: 900px) {
      width: 100%;
    }
    label {
      font-family: Poppins;
      font-weight: bold;
      width: 50%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 15px;
      height: 10%;
      @media screen and (max-width: 700px) {
        width: 100%;
      }
      input,
      textarea {
        width: 100%;
        background: none;
        border: 2px solid $white;
        border-radius: 4px;
        resize: none;
        color: $white;
        font-size: 1em;
        font-weight: bold;
        margin-top: 2%;
        padding: 0 5%;
        &:focus {
          outline: none;
          border: 2px solid $color-3;
        }
      }
      input[type="text"],
      input[type="email"] {
        height: 30px;
      }
      input:-internal-autofill-selected {
        background: transparent;
      }
      textarea {
        height: 100px;
        padding: 2% 5%;
      }
    }
    .btn {
      min-width: 130px;
      height: 40px;
      color: $white;
      padding: 5px 10px;
      font-weight: bold;
      cursor: pointer;
      transition: all 0.3s ease;
      position: relative;
      display: inline-block;
      outline: none;
      border-radius: 20px;
      border: 2px solid $color-3;
      background: $color-3;
      &:hover {
        background: transparent;
        color: $color-3;
      }
    }
  }
  h3 {
    padding-top: 1%;
    font-family: Poppins;
  }
  .hand {
    width: 100%;
    height: 100%;
    @include verticalCenter;
    i {
      font-size: 2em;
      color: $color-3;
      animation: animateHand 2s infinite;
      @keyframes animateHand {
        0% {
          transform: translateY(0);
        }
        25% {
          transform: translateY(50%);
        }
        50% {
          transform: translateY(80%);
        }
        75% {
          transform: translateY(50%);
        }
        100% {
          transform: translateY(0);
        }
      }
    }
  }
}
