.about {
  width: 90%;
  padding: 2%;
  height: 100%;
  margin: 2% 5%;
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.336);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  .aboutContainer {
    width: 100%;
    height: 100%;
    padding: 1%;
    display: flex;
    align-items: center;
    @media screen and (max-width: 1000px) {
      flex-direction: column;
    }
    .leftcontaint {
      width: 50%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      @media screen and (max-width: 1000px) {
        width: 100%;
      }
      h2 {
        text-align: center;
        width: 100%;
        color: $color-3;
      }
      ul {
        width: 100%;
        font-family: Poppins;
        font-weight: 500;
        line-height: 18px;
        li {
          @media screen and (max-width: 600px) {
            font-size: 0.8em;
          }
          @media screen and (max-width: 500px) {
            font-size: 0.7em;
          }
          width: 100%;
          span {
            color: black;
          }
        }
        .listInList {
          padding-left: 15%;
        }
      }
    }
    .rightcontaint {
      width: 50%;
      height: 100%;
      @media screen and (max-width: 1000px) {
        height: 100%;
        width: 100%;
      }
      .d3viewSkill {
        position: relative;
        transform-style: preserve-3d;
        width: auto;
        height: auto;
        animation: animate 20s linear infinite;
        @keyframes animate {
          0% {
            transform: perspective(1000px) rotateX(0deg) rotateY(30deg);
          }
          100% {
            transform: perspective(1000px) rotateX(360deg) rotateY(30deg);
          }
        }
        span {
          position: absolute;
          top: 0%;
          right: 40%;
          transform: translate(-50%, -50%);
          width: 100%;
          height: 100%;
          transform-origin: center;
          transform-style: preserve-3d;
          transform-style: preserve-3d;
          //Valeur à transformer si ajout de span
          transform: rotateX(calc(var(--i) * 45deg)) translateZ(250px);
          width: 200px;
          height: 200px;
        }
        span:nth-child(1) {
          background: url("../../assets/icon/pngwing.com\ \(2\).png")
            center/cover;
        }
        span:nth-child(2) {
          background: url("../../assets/icon/puppeteer.png") center/contain
            no-repeat;
        }
        span:nth-child(3) {
          background: url("../../assets/icon/nodejs.png") center/contain
            no-repeat;
        }
        span:nth-child(4) {
          background: url("../../assets/icon/mongodb.png") center/contain
            no-repeat;
        }

        span:nth-child(5) {
          background: url("../../assets/icon/github2.png") center/cover;
        }
        span:nth-child(6) {
          background: url("../../assets/icon/linux.png") center/contain
            no-repeat;
        }
        span:nth-child(7) {
          background: url("../../assets/icon/seo.png") center/contain no-repeat;
        }
        span:nth-child(8) {
          background: url("../../assets/icon/owasp.png") center/contain
            no-repeat;
        }
        @media screen and (max-width: 1000px) {
          display: none;
        }
      }
    }
    .fixViewSkill {
      display: none;
      width: 100%;
      padding: 5%;
      @media screen and (max-width: 1000px) {
        display: block;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
      }
      span {
        width: 100px;
        height: 100px;
        display: block;
        animation: rotation 6s infinite linear;
        transform-origin: center center;
        padding: 10%;

        @keyframes rotation {
          from {
            transform: rotateY(0deg);
          }
          to {
            transform: rotateY(360deg);
          }
        }
        @media screen and (max-width: 550px) {
          width: 50px;
          height: 50px;
          animation: none;
        }
      }
      span:nth-child(1) {
        background: url("../../assets/icon/pngwing.com\ \(2\).png") center/cover;
      }

      span:nth-child(2) {
        background: url("../../assets/icon/puppeteer.png") center/contain
          no-repeat;
      }
      span:nth-child(3) {
        background: url("../../assets/icon/nodejs.png") center/contain no-repeat;
      }
      span:nth-child(4) {
        background: url("../../assets/icon/mongodb.png") center/cover;
      }

      span:nth-child(5) {
        background: url("../../assets/icon/github2.png") center/cover;
      }
      span:nth-child(6) {
        background: url("../../assets/icon/linux.png") center/contain no-repeat;
      }
      span:nth-child(7) {
        background: url("../../assets/icon/seo.png") center/contain no-repeat;
      }
      span:nth-child(8) {
        background: url("../../assets/icon/owasp.png") center/contain no-repeat;
      }
    }
  }
}
